import * as React from "react";
import { useState, useEffect } from 'react';
import { useLocation, useNavigate } from "react-router-dom";
import { useSnackbar } from 'notistack';

import './Map.css';

import NavbarManager from "./NavbarManager";
import { useAuth } from "../auth";

const server = '192.168.1.5:8999/';
//const httpServer = 'http://' + server;
const httpServer = '/';
const url = httpServer + 'apiV1/generateWallets';

function TokensMap() {
  const [tokens, setTokens] = useState([[]]);

  const { authTokens } = useAuth();
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const location = useLocation();
  const navigate = useNavigate();


  useEffect(() => { loadData(); }, [location]);

  const loadData = () => {
    if (authTokens !== undefined)
    fetchData(authTokens) 
    .then((tokens) => { setTokens(tokens); console.log(tokens); })
    .catch((error) => { enqueueSnackbar('Error connecting to server - ' + error); });
  };

  const onclick = (event) => {
    navigate('/sales/token/' + event.target.id, { state: { token: tokens[event.target.id]  }});
  };
 
  return (
    <>
      <main>
        <div className="image">
          <img src="/map.jpg" id="physicalimage"/>
          <a id="1" onClick={onclick} style={{top: "0%", left: "10%", width: "15%", height: "15%"}}></a>
          <a id="2" onClick={onclick} style={{top: "40%", left: "45%", width: "15%", height: "15%"}}></a>
          <a id="3" onClick={onclick} style={{top: "0%", left: "40%", width: "15%", height: "15%"}}></a>
        </div>
      </main>
    </>
  );
}

function fetchData(jwt) {
  return fetch(httpServer + 'tokens', {
      method: 'get',
      headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'authorization': 'Bearer ' + jwt
      }})
    .then((response) => response.json())
    .then((responseJson) => {
      return (responseJson);
  });
}

export default TokensMap;